

.menu-container {
  display: flex;
  position: fixed;    
      top: 0;
      left: 0;
      padding: 48px 40px;
      width: 100vw;
      height: 100vh;
      flex-direction: column;
      overflow-y: scroll;
      z-index: 100;
      transform: translateX(100%);
      transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
     
      // height: 796px;
      background-color: $whiteColor;
      &.is-open {
       
        transform: translateX(0);
      }

      @media screen and (max-width: 459px) {
        padding: 40px 25px;
      }
      @media screen and (min-width: 768px) {
          display: none;
      }
       }
  
  
  .menu-toggle {
    display: flex;
    
    top: 10px;
    right: 15px;
      min-width: 40px;
      min-height: 40px;
    align-items: center;
    justify-content: center;
    margin-left: auto;
    padding: 0;
    border: none;
    background-color: transparent;
    cursor: pointer;
    
    &:hover,
	&:focus {
	color: $accentColor;
    }
    
   &__icon {
    fill: currentColor;
   }
    
   .menu-container & {
	position: absolute;
	top: 16px;
	right: 16px;
	}
  
    // & .icon-menu__close {
    //   display: none;
    // }
    // &.is-open .icon-menu__close {
    //   display: block;
    // }
    // &.is-open .icon-menu__open {
    //   display: none;
    // }
    @media screen and (min-width: 768px) {
      display: none;
  }
      
  }
  
  .mobile-menu {
    padding-left: 0;
    padding-right: 0;
    list-style: none;
    margin-bottom: auto;
    //   margin-left: 93px;
  
    // &__list {
     
    //   @extend %list;
  
    //   @media screen and (min-weight: 768px) {
    //     display: flex;
    //   }
    // }
  
    &__item {
       
            margin-bottom: 32px;
            //   margin-right: 0;
          
      // position: relative;
      // margin-right: 50px;
      @media screen and (max-width: 459px) {
        margin-bottom: 16px;
      }
      
    }
  
    &__link {
      display: block;
      color: $primaryTextColor;
      text-decoration: none;
      font-size: 40px;
      font-weight: 500;
      line-height: 1.175;
      letter-spacing: 0.02em;
      // line-height: 1.14;
      // padding: 32px 0;
      opacity: 1;
      transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);

      @media screen and (max-width: 459px) {
        font-size: 32px;
      }
  
      &:hover,
      &:focus {
        color: $accentColor;
      }
      &--current {
        color: $accentColor;
  
        // &::after {
        //   @media screen and (min-width: 768px) {
        //     position: absolute;
        //     content: '';
        //     width: 100%;
        //     left: 0;
        //     bottom: 0;
        //     height: 4px;
        //     border-radius: 2px;
        //     background-color: $accentColor;
        //   }
        // }
      }
    }
  }
 
  .contacts-menu {
    @extend %list;
    margin-bottom: 32px;
    // display: flex;
    // flex-direction: column-reverse;
    // gap: 32px;
    // justify-content: flex-end;
    // gap: 50px;
&__item {
margin-bottom: 32px;
}

    &__link {
      // display: flex;
      // align-items: center;
      
      text-decoration: none;
     
  
      font-style: normal;
  
      // transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  
      // &:hover,
      // &:focus {
      //   color: $accentColor;
      // }
    }
  
    &__icon {
      fill: currentColor;
      margin-right: 10px;
      display: none;
    }
  
    &__tel {
      color: $mobContactsColor;
      font-weight: 500;
      line-height: 1.176;
      font-size: 34px;
      letter-spacing: 0.02em;
      &:hover,
      &:focus {
        color: $accentColor;
      }
      
      @media screen and (max-width: 459px) {
        font-size: 24px;
      }
  
    }
    &__mail {
      color: $secondaryTextColor;
      font-weight: 500;
      font-size: 24px;
